@import "resets.scss";
@import "normalizez.scss";


$color-menu: #656565;
$color-menu-blue: #043b6f;
$color-red: #ff5151;
$color-menu-hover: #9ae7ff;
$color-white: #e3e3e3;


* {
    box-sizing: border-box; }

@font-face {
    font-family: "MManrope";
    src: url("../fonts/Manrope-Regular.ttf");
    font-weight: regular;
    font-style: normal; }

@font-face {
    font-family: "MManrope";
    src: url("../fonts/Manrope-Bold.ttf");
    font-weight: bold;
    font-style: normal; }

html {
    font-family: "MManrope";
    font-weight: regular;
    scroll-behavior: smooth; }

.container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center; }

.text {
    color: #e3e3e3; }

.header {
    position: sticky;
    top: 0;
    min-height: 100px;
    background: #043b6f;
    padding-top: 25px;
    z-index: 5;
    &__container {
        width: 1100px;
        margin: 0 auto; }
    &__logo-box {
        margin-left: 35px;
        width: 350px;
        filter: drop-shadow(8px 4px 4px rgba(0, 0, 0, 1)); } }

.nav-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;  //расстояние между основными кнопками меню и Войти
    width: 600px;
    height: 35px;
    &__right {
        margin-right: 35px; }
    &__list {
        display: flex;
        flex-direction: row;
        text-align: center;
        a {
            cursor: default; } }
    &__list-item {
        margin-right: 25px;
        font-size: 18px; }
    &__login {
        font-size: 18px; }
    &__text {
        text-decoration: none;
        color: white; }
    &__text:hover {
        color: $color-menu-hover; } }

.fa-solid {
    font-size: 14px; }
.dropdown-menu {
    --bs-dropdown-item-padding-x: 0.7rem;
    --bs-dropdown-padding-y: 0.2rem;
    --bs-dropdown-divider-margin-y: 0.2rem;
    background: #e7e7e7; }

.dropdown-item {
    color: #000000;
    &__email:hover {
        --bs-dropdown-link-hover-bg: #e7e7e7; } }

.btn-secondary {
    --bs-btn-color: #fff;
    --bs-btn-bg: $color-menu-blue;
    --bs-btn-border-color: $color-menu-blue;
    --bs-btn-hover-color: #9ae7ff;
    --bs-btn-hover-bg: $color-menu-blue;
    --bs-btn-hover-border-color: $color-menu-blue;
    --bs-btn-focus-shadow-rgb: 130,138,145;
    --bs-btn-active-color: #9ae7ff;
    --bs-btn-active-bg: $color-menu-blue;
    --bs-btn-active-border-color: $color-menu-blue;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #fff;
    --bs-btn-disabled-bg: $color-menu-blue;
    --bs-btn-disabled-border-color: $color-menu-blue; }

.footer {
    background: black;
    &__container {
        min-height: 110px;
        width: 1000px;
        margin: 0 auto; }
    &__logo-box {
        width: 300px; }
    &__contacts-box {
        display: flex;
        flex-direction: row;
        font-size: 18px; }
    &__item {
        margin-right: 50px;
        &:last-child {
            margin-right: 0px; } }
    &__contacts-title {
        margin-bottom: 5px; } }

@media (max-width: 1199px) {
    .header {
        &__container {
            width: 880px; }
        &__logo-box {
            width: 300px; } }
    .nav-menu {
        width: 450px;
        &__list-item {
            width: 90px;
            margin-right: 20px;
            font-size: 16px; }
        &__list-login {
            font-size: 16px; } }
    .fa-solid {
        font-size: 14px; }
    .dropdown-menu {
        --bs-dropdown-min-width: 0px;
        --bs-dropdown-item-padding-x: 0.7rem;
        --bs-dropdown-padding-y: 0.2rem;
        --bs-dropdown-divider-margin-y: 0.2rem; }
    .dropdown-item {
        font-size: 14px; }
    .footer {
        &__container {
            width: 850px; }
        &__logo-box {
            width: 250px; }
        &__contacts-box {
            font-size: 14px; } } }

@media (max-width: 991px) {
    .header {
        &__container {
            width: 720px; }
        &__logo-box {
            width: 250px; } }
    .nav-menu {
        width: 400px;
        &__list-item {
            margin-right: 15px;
            font-size: 14px; }
        &__list-login {
            font-size: 14px; } }
    .dropdown-menu {
        --bs-dropdown-item-padding-x: 0.7rem;
        --bs-dropdown-padding-y: 0.2rem;
        --bs-dropdown-divider-margin-y: 0.2rem; }
    .footer {
        &__container {
            width: 690px;
            min-height: 75px; }
        &__logo-box {
            width: 200px; }
        &__contacts-box {
            font-size: 12px; }
        &__item {
            margin-right: 30px;
            &:last-child {
                margin-right: 0px; } } } }

@media (max-width: 767px) {
    .header {
        min-height: 80px;
        &__container {
            width: 540px; }
        &__logo-box {
            width: 150px; } }
    .nav-menu {
        width: 310px;
        &__list-item {
            width: 70px;
            margin-right: 10px;
            font-size: 12px; }
        &__list-login {
            font-size: 12px; } }
    .fa-solid {
        font-size: 12px; }
    .dropdown-menu {
        --bs-dropdown-min-width: 0px;
        --bs-dropdown-item-padding-x: 0.3rem;
        --bs-dropdown-padding-y: 0.0rem;
        --bs-dropdown-divider-margin-y: 0.1rem; }
    .dropdown-item {
        font-size: 12px; }
    .footer {
        &__logo-box {
            width: 10px;
            display: none; }
        &__item {
            margin-right: 55px;
            &:last-child {
                margin-right: 25px; } } } }

@media (max-width: 575px) {
    .header {
        min-height: 75px;
        &__container {
            width: 100%; }
        &__logo-box {
            width: 180px; } }
    .nav-menu {
        position: fixed;
        top: 0;
        right: -100%;
        height: 100vh;
        width: 50%;
        background: #0073a1;
        transition: 0.6s all;
        display: block;
        &__superstructure {
            height: 70px; }
        &__right {
            margin: 0; }
        &__list {
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            height: 100%;
            align-items: center; }
        &__list-item {
            width: 90px;
            font-size: 16px;
            margin-bottom: 15px; }
        &__list-login {
            font-size: 16px; } }
    .fa-solid {
        font-size: 14px; }
    .dropdown-item {
        font-size: 14px; }
    .nav-menu_active {
        display: block;
        right: 0; }
    .hamburger {
        height: 14px;
        width: 25px;
        position: absolute;
        top: 35px;
        right: 30px;
        z-index: 10;
        span {
            height: 2px;
            width: 100%;
            display: block;
            background-color: $color-white;
            margin-bottom: 4px;
            transition: 1s all;
            &:last-child {
                margin-bottom: 0px; } } }
    .hamburger_active {
        z-index: 10;
        span {
            margin-bottom: -2px;
            &:nth-child(1) {
                transform: translateY(3px) rotate(-45deg); }
            &:nth-child(2) {
                display: none; }
            &:nth-child(3) {
                transform: translateY(3px) rotate(45deg); } } }
    .footer {
        &__logo-box {
            width: 10px;
            display: none; }
        &__container {
            min-height: 65px;
            width: 400px; }
        &__contacts-box {
            font-size: 10px; }
        &__item {
            margin-right: 15px;
            &:last-child {
                margin-right: 15px; } } } }

@media (max-width: 460px) {
    .header {
        min-height: 70px;
        &__logo-box {
            width: 120px; } }
    .footer {
        &__container {
            min-height: 60px;
            width: 300px; }
        &__contacts-box {
            font-size: 8px; }
        &__item {
            margin-right: 10px;
            &:last-child {
                margin-right: 0; } } } }

@media (max-width: 319px) {
    .header {
        overflow: hidden; }
    .nav-menu {
        &__left {
            margin-bottom: 5px; }
        &__user {
            width: 100%; } }
    .footer {
        overflow: hidden; } }

